<script>
import { Form, Field, ErrorMessage } from 'vee-validate';
import { localize } from '@vee-validate/i18n';
import constant from '@/utils/constant';
export default {
  components: {
    Form,
    Field,
    ErrorMessage,
  },
  data() {
    return {
      gameData: {
        name: '',
        roundList: [
          {
            isRhyme: false,
            timer: null,
            sampleElement: '',
            challengeElement: [{ isTrue: false, value: '', quantity: null }],
          },
        ],
      },
      gameId: null,
    };
  },
  mounted() {
    this.checkEdit();
    // custom error message
    localize('vi', {
      fields: {
        game_name: {
          required: 'Vui lòng nhập tên trò chơi',
        },
        'sample_element[1]': {
          required: 'Vui lòng nhập tên trò chơi',
        },
      },
    });
  },
  methods: {
    /**
     * Check edit
     */
    checkEdit() {
      const routeName = this.$route.name;
      if (routeName == 'game_edit') {
        this.gameId = this.$route?.params?.id;
        if (this.gameId) {
          try {
            const gameData = localStorage.getItem(constant.LOCAL_STORAGE_KEY.GAME_PICK_LETTER + this.gameId) || null;
            // Check data existence
            if (!gameData) {
              this.$notify({
                text: 'Dữ liệu game không tồn tại!', // Notify if game data does not exist
                type: 'error',
              });
              return this.$router.push({'name': 'game'});
            }
            this.gameData = JSON.parse(gameData);
            return true;
          } catch (e) {
            this.$notify({
              text: 'Dữ liệu game không tồn tại!', // Notify if an error occurs while getting game data
              type: 'error',
            });
            return this.$router.push({'name': 'game'});
          }
        } else {
          this.$notify({
            text: 'Dữ liệu game không tồn tại!', // Notify if an error occurs while getting game data
            type: 'error',
          });
          return this.$router.push({'name': 'game'});
        }
      }
    },
    /**
     * Submit form
     */
    onSubmit() {
      this.createGame();
    },
    /**
     * Create game data to local storage
     */
    createGame() {
      // check validation
      const isValidRoundList = this.validateRoundList(this.gameData.roundList);
      if (!isValidRoundList.result) {
        let text = 'Số từ thử thách tối thiểu phải bằng số từ cần tìm. Vui lòng kiểm tra lại!';
        if (isValidRoundList.isRhyme) {
          text = 'Số từ thử thách tổi thiểu phải chứa đủ số vần cần tìm. Vui lòng kiểm tra lại!';
        }
        this.$notify({
          text: text,
          type: 'error',
        });
        return false;
      }
      // update or create
      const gameId = constant.LOCAL_STORAGE_KEY.GAME_PICK_LETTER + (this.gameId ? this.gameId : this.generateId());
      localStorage.setItem(gameId, JSON.stringify(this.gameData));
      this.$router.push({ name: 'game' });
    },
    /**
     * Add a element
     */
    addElement(challengeElement) {
      challengeElement.push({ isTrue: false, value: '', quantity: null });
    },
    /**
     * Remove a element
     */
    removeElement(challengeElement, index) {
      if (challengeElement.length > 1) {
        challengeElement.splice(index, 1);
      }
    },
    /**
     * check show remove button
     */
    shouldShowRemoveButton(array) {
      return array.length > 1;
    },
    /**
     * add a round
     */
    addRound() {
      this.gameData.roundList.push({
        isRhyme: false,
        timer: null,
        sampleElement: '',
        challengeElement: [{ isTrue: false, value: '', quantity: null }],
      });
    },
    /**
     * remove round
     */
    removeRound(roundIndex) {
      if (this.gameData.roundList.length > 1) {
        this.gameData.roundList.splice(roundIndex, 1);
      }
    },
    /**
     * random id game
     */
    generateId() {
      const characters = 'abcdefghijklmnopqrstuvwxyz0123456789';
      let id = '';

      for (let i = 0; i < 8; i++) {
        const randomIndex = Math.floor(Math.random() * characters.length);
        id += characters.charAt(randomIndex);
      }
      return id;
    },
    /**
     * check challenge value
     */
    checkChallengeValue(element, sampleElement, isRhyme) {
      if (!isRhyme && sampleElement) { // skip if rhyme
        const sampleElements = sampleElement.split(',');
        if (sampleElements.includes(element.value)) {
          element.isTrue = true;
        } else {
          element.isTrue = false;
        }
      }
    },
    /**
     * check sample element
     */
    checkSampleElement(round) {
      if (!round.isRhyme && round.sampleElement) {
        const sampleElements = round.sampleElement.split(',');
        round.challengeElement.forEach((item) => {
          if (item.value) { // value not null
            if (sampleElements.includes(item.value)) { // if value includes sample elements
              item.isTrue = true;
            } else {
              item.isTrue = false;
            }
          } else {
            item.isTrue = false;
          }
        })
      }
    },
    /**
     * convert challenge element to string
     */
    convertChallengeElementToString(challengeElement) {
      return challengeElement.map(element => element.value).join('-');;
    },
    /**
     * validates round list
     */
    validateRoundList(roundList) {
      for (let i = 0; i < roundList.length; i++) {
        const round = roundList[i];
        const isValid = this.isValidChallengeElement(
          round.sampleElement,
          round.challengeElement,
          round.isRhyme,
        );

        if (!isValid) {
          return { result: false, isRhyme: round.isRhyme}; // return if not valid
        }
      }

      return { result: true, isRhyme: false}; // return true if valid
    },
    /**
     * compare data
     */
    isValidChallengeElement(sampleElement, challengeElement, isRhyme) {
      const sampleElements = sampleElement.split(',');
      const challengeValues = challengeElement.map((item) => item.value);

      if (isRhyme) {
        const count = challengeElement.reduce((acc, element) => {
          if (element.isTrue === true) {
            return acc + 1;
          } else {
            return acc;
          }
        }, 0);

        return sampleElements.length === count;
      } else {
        // Non-rhyme mode: Check if all sample elements are included in challenge values
        return sampleElements.every((value) => challengeValues.includes(value));
      }
    },
  },
};
</script>
